<template>
  <h1 id="writeHead">블로그 글 작성</h1>
  <div class="input-group mb-3">
    <span class="input-group-text" id="inputGroup-sizing-default">제목</span>

    <input
      type="text"
      class="form-control"
      id="inputGroup-sizing-default2"
      aria-label="Sizing example input"
      aria-describedby="inputGroup-sizing-default"
      v-model="title"
    />
  </div>
  <div id="editor"></div>
  <input type="file" id="image" accept="image/*" />
  <button class="btn btn-primary" id="sub" @click="subData">작성완료</button>
</template>

<script>
import Editor from '@toast-ui/editor'
import '@toast-ui/editor/dist/toastui-editor.css' // Editor style
import 'firebase/firestore'
import 'firebase/storage'
import 'base-64'
import firebase from 'firebase/app'
import { db } from '../main'

export default {
  name: 'editorWrite',
  data() {
    return {
      editor: null,
      localDisplayuid: '',
      title: '',
    }
  },
  mounted() {
    this.editor = new Editor({
      el: document.querySelector('#editor'),
      height: '500px',
      width: 'auto',
      initialEditType: 'wysiwyg',
      hooks: {},
    })
  },
  methods: {
    subData() {
      console.log(this.editor.getHTML())
      const storage = firebase.storage()
      var file = document.querySelector('#image').files[0]
      var uploadImg = storage
        .ref()
        .child('image/' + file.name)
        .put(file)
      uploadImg.on(
        'state_changed',
        null,
        (error) => {
          console.error('실패사유는', error)
        },
        () => {
          uploadImg.snapshot.ref.getDownloadURL().then((url) => {
            console.log('업로드된 경로는', url)
            var saveUser = localStorage.getItem('user')
            this.localDisplayuid = JSON.parse(saveUser).uid
            var postData = {
              content: this.editor.getHTML(),
              date:
                new Date().getFullYear() +
                '-' +
                (new Date().getMonth() + 1) +
                '-' +
                new Date().getDate() +
                ' ' +
                new Date().getHours() +
                ':' +
                new Date().getMinutes(),
              title: this.title,
              imgURL: url,
              storageURL: 'image/' + file.name,
              uid: this.localDisplayuid,
            }
            db.collection('post')
              .add(postData)
              .then(() => {
                alert('등록에 성공했습니다')
                window.location.href = '/'
              })
              .catch((err) => {
                console.log(err)
              })
          })
        }
      )
    },
  },
}
</script>

<style></style>
